@use 'base/variables' as v;

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin  desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin  desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}
@mixin  desktopXXL {
    @media (min-width: v.$desktopXXL) {
        @content;
    }
}
@mixin  desktopXXXL {
    @media (min-width: v.$desktopXXXL) {
        @content;
    }
}
@mixin grid($columnas, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}
@mixin boton($color, $texto) {
    background-color: $color;
    font-size: 2rem;
    padding: 1.2rem 3rem;
    border-radius: 1rem;
    font-weight: 700;
    display: block;
    width: 100%;
    text-align: center;
    color: $texto;
    text-decoration: none;
    transition: background-color .3s ease;
    border: none;

    &:hover {
        cursor: pointer;
        background-color: darken($color, 10);
    }
    @include tablet {
        width: auto;
    }
}